import axios from 'axios'
import { BASE_URL, LOGIN } from '../constants/api.config'
import { toastConfig } from '../constants/other.config'
import toast from 'react-hot-toast'
import { saveJWTinCookie } from '../utils/handleCookie'
import { errorHandler } from '../utils/errorHandler'

export default function login(data) {
  const config = {
    method: 'post',
    url: `${BASE_URL}/${LOGIN}`,
    headers: { 'Content-Type': 'application/json' },
    data,
  }

  return new Promise((resolve, reject) => {
    axios(config)
      .then((result) => {
        saveJWTinCookie(result.data)
        resolve(result.data)
      })
      .catch((error) => {
        reject(error)
        toast.error(errorHandler(error?.response?.status), toastConfig)
      })
  })
}
